const baseURL = 'http://localhost:8080'//url

export const courseRequest = (options) =>{
    //loading
	uni.showLoading({
		title:''
	})

    return new Promise((resolve, reject) => {
        uni.request({
            //  + 
			url: baseURL + options.url,
			//  GET 
			method: options.method || 'GET',
			//  {} 
			data: options.data || {},
			// 

            success:(res)=>{
            	if(res.data.code === 40000)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else if(res.data.code === 40001)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else if(res.data.code === 40100)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else if(res.data.code === 40101)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else if(res.data.code === 40301)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else if(res.data.code === 50000)
				{
					uni.showToast({
						title: '',
						icon: 'error',
						mask: true
					})
				}else
				{
					resolve(res);
				}



            },

            //
            fail: (err) => {
				reject(err)
			},

            //
			complete() {
				//loading
				uni.hideLoading()
			}

        })
    })

}